<template>
  <div>
    <Head-Frame></Head-Frame>
    <Body-Gui></Body-Gui>
    <Bottom-Bar></Bottom-Bar>
  </div>
</template>

<script>
import HeadFrame from './components/Head-Frame.vue'
import BodyGui from './components/Body-Gui.vue'
import BottomBar from './components/BottomBar.vue'
import $ from 'jquery'

export default {
  name: 'App',
  components: {
    HeadFrame,
    BodyGui,
    BottomBar,
  },
  mounted() {
    $(function () {
      var shikou = document.documentElement.clientWidth
      if (shikou > 730) {
        loadMain() //
      }
    })

    // setInterval("loadMain()",300000); //5;

    var firstFlag = true //
    var isFullScreen = 0 //01
    var systemRatioOld = window.devicePixelRatio //
    var systemRatioNew = window.devicePixelRatio //
    var defaultRatio = 1.0 //
    var fullScreenRatio = 1.0 //
    var setZoom = defaultRatio / systemRatioNew //zoom

    //
    function autoZoom() {
      systemRatioNew = window.devicePixelRatio //

      if (isFullScreen == 0) {
        setZoom = defaultRatio / systemRatioNew //zoom
      } else if (isFullScreen == 1) {
        setZoom = fullScreenRatio / systemRatioNew //zoom
      }

      setZoom = setZoom.toFixed(6) //6
      setZoom = parseFloat(setZoom) //0
      if (setZoom != document.body.style.zoom) {
        document.body.style.zoom = setZoom
      }
    }

    //
    function loadMain() {
      systemRatioNew = window.devicePixelRatio //

      if (systemRatioOld != systemRatioNew || firstFlag) {
        firstFlag = false
        systemRatioOld = systemRatioNew
        // console.log(":"+systemRatioNew+" zoom:"+document.body.style.zoom+" zoom:"+parseFloat((fullScreenRatio/systemRatioNew).toFixed(6)));
        document.body.style.zoom = fullScreenRatio / systemRatioNew //
      }

      /*
        function 
    */

      autoZoom()
    }
  },
}
</script>

